<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code>The url of the Bing Maps server hosting the imagery.</td>
</tr>
<tr>
<td><strong>bmKey</strong></td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code>The Bing Maps key for your application, which can be created at <a href="https://www.bingmapsportal.com/">https://www.bingmapsportal.com/</a>. If this parameter is not provided, BingMapsApi.defaultKey is used, which is undefined by default.</td>
</tr>
<tr>
<td>tileProtocol</td>
<td>String</td>
<td></td>
<td><code v-pre>optional</code>The protocol to use when loading tiles, e.g. 'http:' or 'https:'. By default, tiles are loaded using the same protocol as the page.</td>
</tr>
<tr>
<td>mapStyle</td>
<td>String</td>
<td><code v-pre>'Aerial'</code></td>
<td><code v-pre>optional</code>The type of Bing Maps imagery to load.</td>
</tr>
<tr>
<td>culture</td>
<td>String</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code>The culture to use when requesting Bing Maps imagery. Not all cultures are supported. See <a href="http://msdn.microsoft.com/en-us/library/hh441729.aspx">http://msdn.microsoft.com/en-us/library/hh441729.aspx</a> for information on the supported cultures.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The ellipsoid. If not specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>tileDiscardPolicy</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The policy that determines if a tile is invalid and should be discarded. If this value is not specified, a default DiscardMissingTileImagePolicy is used which requests tile 0,0 at the maximum tile level and checks pixels (0,0), (120,140), (130,160), (200,50), and (200,200). If all of these pixels are transparent, the discard check is disabled and no tiles are discarded. If any of them have a non-transparent color, any tile that has the same values in these pixel locations is discarded. The end result of these defaults should be correct tile discarding for a standard Bing Maps server. To ensure that no tiles are discarded, construct and pass a NeverTileDiscardPolicy for this parameter.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official documentation: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BingMapsImageryProvider.html">BingMapsImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the imagery provider encounters an asynchronous error.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Triggers when the provider is ready for use.</td>
</tr>
</tbody>
</table>
